import React from "react";

import "../../../../index.scss";
import styles from "./Rules.module.scss";
import AppContext from "../../../../context";
import data from "../../../../JSON/rules.json";

function Rules() {
  const { clientAgree, setClientAgree } = React.useContext(AppContext);

  return (
    <div className="container_no-top">
      <div>
        <div className={styles.detailText}>
          <p>
                 .   
               .
          </p>
          <p>
                  ,  
             (24 ),      
            .    .
          </p>
          <p>
            ,  ,    
            .
          </p>
          {data.map((item) => {
            return (
              <p>
                <b>{item.TITLE}</b> {item.TEXT}
              </p>
            );
          })}
        </div>
        <div class="checkbox checkbox_mini checkbox__read-all">
          <button
            type="button"
            className={clientAgree && "buttonActive"}
            onClick={() => setClientAgree(!clientAgree)}
          >
            {clientAgree && <img src="/img/check.svg" alt="check svg"></img>}
          </button>
          <span>
                    
            <a href="/offer" target="_blank">
              
            </a>
          </span>
        </div>
      </div>
    </div>
  );
}

export default Rules;
